import React from "react";
import { ListItem, ListItemText, ListItemGraphic } from "@material/react-list";
import { Section, Schedule, SchedulesState } from "../redux/types";
import { Checkbox } from "@material/react-checkbox";
import { addSection, removeSection } from "../redux/actions";
import { connect } from "react-redux";

export interface SectionListItemProps {
  section: Section;
  addSection?: any;
  removeSection?: any;
  selectedCrns?: Set<number>;
}

interface GradeDistributionProps {
  historical_performance: { [key: string]: (number | null) };
}

const LETTER_MAP: { [key: string]: string } = {
  "A": "#00BCD4",
  "B": "#A5D6A7",
  "C": "#FFEE58",
  "D": "#E57373",
  "F": "#90A4AE",
  "Q": "#D1C4E9",
}

const GradeDistribution: React.FunctionComponent<
  GradeDistributionProps
> = props => {
  const { historical_performance } = props;
  if (historical_performance['A'] === null) {
    return <div />;
  } else {
    const divs = [];
    let sum = 0;
    for (const letter in LETTER_MAP) {
      const val = historical_performance[letter];
      sum += val ? val : 0;
    }
    for (const letter in LETTER_MAP) {
      const val = historical_performance[letter];
      if (val) {
        divs.push(<div key={letter} style={{
          backgroundColor: LETTER_MAP[letter],
          width: `${((val / sum) * 100 - 0.1).toFixed(2)}%`,
          display: "inline-block",
          border: "1px solid white",
          boxSizing: "border-box",
          height: 20
        }}></div>)
      }
    }
    return <div style={{ width: '40%', marginLeft: 'auto' }}>{divs}</div>;
  }
};

class SectionListItem extends React.Component<
  SectionListItemProps
  > {

  constructor(props: SectionListItemProps) {
    super(props);
    this.checkboxClickHandler = this.checkboxClickHandler.bind(this);
    this.isChecked = this.isChecked.bind(this);
    this.checkboxClickHandler = this.checkboxClickHandler.bind(this);
  }

  isChecked(): boolean {
    if (this.props.selectedCrns) {
      return this.props.selectedCrns.has(this.props.section.crn);
    }
    return false;
  }

  checkboxClickHandler() {
    if (this.isChecked()) {
      this.props.removeSection(this.props.section.crn);
    } else {
      this.props.addSection(this.props.section.crn);
    }
  }
  render() {
    const { section_num, instructor, historical_performance } = this.props.section;
    return (
      <ListItem>
        <Checkbox initRipple={() => { }} checked={this.isChecked()} onChange={this.checkboxClickHandler} />
        <ListItemText primaryText={section_num} secondaryText={instructor} />
        <ListItemGraphic graphic={<GradeDistribution historical_performance={historical_performance} />} />
      </ListItem>
    );
  }
}

const mapDispatchToProps = (dispatch: any) => {
  return {
    addSection: (sectionCrn: number) => dispatch(addSection(sectionCrn)),
    removeSection: (sectionCrn: number) => dispatch(removeSection(sectionCrn))
  }
}

const mapStateToProps = (state: SchedulesState) => {
  let selectedCrns = new Set<number>();
  if (state.currScheduleName) {
    const schedule = state.schedules.get(state.currScheduleName);
    if (schedule) {
      selectedCrns = schedule.selectedCrns;
    }
  }
  return { selectedCrns };
}

export default connect(mapStateToProps, mapDispatchToProps)(SectionListItem);