const initialState: SchedulesState = {
  schedules: new Map<string, Schedule>(),
  // schedules: x,
  currScheduleName: undefined
};

const timeStringToMinSinceMidnight = (timeString: string): number => {
  const [hour, minute, second] = timeString.split(":").map((chunk) => Number.parseInt(chunk));
  return (hour * 60) + minute + (second / 60);
}


export function reducer(
  state = initialState,
  action: ActionTypes
): SchedulesState {
  timeStringToMinSinceMidnight("08:00:00")
  let newState = { ...state };
  const { currScheduleName } = newState;
  if (action.type === SELECT_SCHEDULE) {
    newState.currScheduleName = action.payload;
  }
  if (currScheduleName) {
    const schedule = newState.schedules.get(currScheduleName);
    if (schedule) {
      switch (action.type) {
        case ADD_SECTION:
          schedule.selectedCrns.add(action.payload);
          break;
        case REMOVE_SECTION:
          schedule.selectedCrns.delete(action.payload);
          break;
        case ADD_COURSE:
          schedule.courses[`${action.payload.dept}-${action.payload.course_num}`] = action.payload;
          break;
        case REMOVE_COURSE:
          const { dept, course_num } = action.payload;
          delete schedule.courses[`${dept}-${course_num}`];
          newState.schedules.set(currScheduleName, schedule);
          break;
        case REMOVE_SCHEDULE:
          newState.schedules.delete(action.payload);
          break;
      }
    } else {
      if (action.type === ADD_SCHEDULE) {
        // const newSchedule: Schedule = {
        //   name: action.payload.name,
        //   term_code: action.payload.term_code,
        //   i
        //   courses: {},
        //   selectedCrns: new Set<number>()
        // };
        // newState.schedules.set(action.payload.name, newSchedule);
      } else {
        throw Error("The schedule attempting to be accessed does not exist.");
      }
    }
  } else {
    if (action.type === LOAD_SCHEDULES) {
      for (let scheduleName in action.payload) {
        let schedule = action.payload[scheduleName];
        for (let courseName in schedule.courses) {
          const course = schedule.courses[courseName];
          for (let section of course.sections) {
            for (let meeting of section.meetings) {
              if (meeting.start_time) {
                meeting.start_time = timeStringToMinSinceMidnight(meeting.start_time);
              }
              if (meeting.end_time) {
                meeting.end_time = timeStringToMinSinceMidnight(meeting.end_time);
              }
            }
          }
        }
        schedule.selectedCrns = new Set<number>(schedule.sections.map((section: string) =>
          Number.parseInt(section.split("_")[0])
        ));
        delete schedule.sections;
        newState.schedules.set(scheduleName, schedule);
      }
    }
  }
  console.log(newState);
  return newState;
}
