import React, { Component } from "react";
import Drawer, {
  DrawerAppContent,
  DrawerHeader,
  DrawerContent
} from "@material/react-drawer";
import AddCourseDialog from "./components/AddCourseDialog";
import AddScheduleDialog from "./components/AddScheduleDialog";
import CourseList from "./components/CourseList";
import Fab from "@material/react-fab";

import MaterialIcon from "@material/react-material-icon";
import ScheduleSelect from "./components/ScheduleSelect";
import { SchedulesState, Schedule } from "./redux/types";
import { connect } from "react-redux";
import { loadSchedules } from "./redux/actions";
import Week from "./components/Week";

interface AppProps {
  anySchedulesCreated: boolean;
  schedules: Map<string, Schedule>;
  currScheduleName: string;
  loadSchedules: any;
}

class App extends Component<AppProps> {
  state = { addCourseDialogIsOpen: false, addScheduleDialogIsOpen: false };

  constructor(props: any) {
    super(props);
    this.handleClick = this.handleClick.bind(this);
    this.closeCourseDialog = this.closeCourseDialog.bind(this);
  }

  componentDidMount() {
    fetch("/api/schedules/")
      .then(response => response.json())
      .then(json => {
        this.props.loadSchedules(json);
      }).catch((err) => console.log(err));
  }

  handleClick() {
    if (this.props.anySchedulesCreated) {
      this.setState({ addCourseDialogIsOpen: true });
    } else {
      this.setState({ addScheduleDialogIsOpen: true });
    }
  }

  closeCourseDialog() {
    this.setState({ addCourseDialogIsOpen: false });
  }
  render() {
    const { schedules } = this.props;
    return (
      <div className="drawer-container">
        <Drawer className="drawer">
          <DrawerHeader>
            <ScheduleSelect scheduleNames={Array.from(schedules.keys())} />
          </DrawerHeader>

          <DrawerContent className="drawer-content">
            <CourseList />
          </DrawerContent>
        </Drawer>
        <DrawerAppContent className="drawer-app-content">
          <div className="week">
            <Week />
          </div>
          <Fab
            className="fab"
            icon={<MaterialIcon icon="add" />}
            onClick={this.handleClick}
          />
        </DrawerAppContent>
        <AddScheduleDialog open={this.state.addScheduleDialogIsOpen} />
        <AddCourseDialog open={this.state.addCourseDialogIsOpen} closeCourseDialog={this.closeCourseDialog}/>
      </div>
    );
  }
}

const mapStateToProps = (state: SchedulesState) => ({
  anySchedulesCreated: Array.from(state.schedules.keys()).length !== 0,
  schedules: state.schedules,
  currScheduleName: state.currScheduleName
});

const mapDispatchToProps = (dispatch: any) => {
  return {
    loadSchedules: (schedules: Array<any>) => dispatch(loadSchedules(schedules))
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(App);
