import React, { Component } from "react";
import Dialog, {
  DialogTitle,
  DialogContent,
  DialogFooter,
  DialogButton
} from "@material/react-dialog";
import TextField, { HelperText, Input } from "@material/react-text-field";
import List, { ListItem, ListItemText } from "@material/react-list";
import MaterialIcon from "@material/react-material-icon";
import { debounce } from "lodash";
import { connect } from "react-redux";
import { getCookie } from "../utils";
import { addCourse } from "../redux/actions";

class AddCourseDialog extends Component {

  state = { value: "", results: [] };

  constructor(props) {
    super(props);
    this.handleChange = this.handleChange.bind(this);
    this.dialogClosing = this.dialogClosing.bind(this);
    this.search = debounce(this.search, 300).bind(this);
    this.resultSelected = this.resultSelected.bind(this);
  }

  resultSelected(selectedIndex) {
    const { dept, course_num } = this.state.results[selectedIndex];
    const body = {
      course_id: `${dept}-${course_num}`
    }
    const csrftoken = getCookie("csrftoken");
    const headers = {
      'Content-Type': 'application/json',
      "X-CSRFToken": csrftoken
    }
    const that = this;
    fetch(`/api/schedules/${this.props.currScheduleId}/courses`, { method: "PATCH", mode: "same-origin", headers, body: JSON.stringify(body) })
      .then((response) => response.json())
      .then((json) => {
        that.props.addCourse(json);
      })
    this.dialogClosing();
  }

  search() {
    const that = this;
    fetch(`/api/data/search?q=${this.state.value}`).then((response) => response.json()).then((json) => {
      that.setState({ results: json });
    })
    // this.setState({ results: [{ "name": "Qualitative Methodologies in Educational Research", "dept": "EPSY", "course_num": "603", "description": "Survey of qualitative research methodologies commonly used in educational research; purposes, epistemologies, data collection and analyses that distinguish different qualitative approaches to educational research; ethical and procedural challenges of collecting qualitative data in educational settings." }, { "name": "Qualitative Rsch Mthds", "dept": "EHRD", "course_num": "655", "description": "Introduction to qualitative research methods; theoretical underpinnings; the research paradigm and applied experience with the methodology." }, { "name": "Qualitative Methods", "dept": "SOCI", "course_num": "229", "description": "Methodologies in social research with emphasis on qualitative dimensions of inquiry; topics include in-depth interviewing, observation, unobtrusive measures, analysis of documents, fieldwork issues, ethics, note-taking, preliminary data analysis, and an overview of writing research reports based on qualitative research." }, { "name": "Qual Res Design And Data Coll", "dept": "EPSY", "course_num": "633", "description": "Introduction to qualitative designs used to answer educational, psychological, or social research questions; historical foundations, epistemologies and essential elements of prevalent qualitative research designs; methods of collecting qualitative data including interviews, naturalistic observation, participant-observation, and stimulated recall procedures." }, { "name": "Qual Rsrch Mthds In C&I", "dept": "EDCI", "course_num": "605", "description": "Theoretical and methodological issues related to qualitative inquiry; discussion of qualitative paradigm's ontological, epistemological, and axiological stances; review and implementation of commonly used qualitative research methods and approaches in curriculum and instruction, including narrative, phenomenology, ethnography, grounded theory, and case study approaches." }, { "name": "Qualitative Analysis", "dept": "CHEM", "course_num": "114", "description": null }, { "name": "Qualitative Method", "dept": "SOCI", "course_num": "624", "description": "Exposure to and critical assessment of qualitative approaches to data gathering in social science; topics include naturalistic observation, field research skills, unobtrusive measures and grounded theory construction." }, { "name": "Data Analysis In Geog", "dept": "GEOG", "course_num": "312", "description": "Foundation for collection and analysis of quantitative and qualitative geographic data; emphasis on hands-on, practical experience with commonly used analysis software and qualitative methods including interviewing and archival research; problems commonly encountered in dealing with data." }, { "name": "Life History Research", "dept": "EHRD", "course_num": "657", "description": "Examines qualitative research that focuses on life experience both in its entirety (life history; biography and autobiography) and with specificity around a particular event (autoethnography); explores the nature of these types of qualitative research and discussing the methodological issues inherent in each mode." }, { "name": "Meth Of Soc Research", "dept": "SOCI", "course_num": "220", "description": "Relationships between sociological theory, research, qualitative evaluation of data; construction and use of analytical procedures and research techniques, and participant observation." }] })
  }

  handleChange(e) {
    this.setState({ value: e.currentTarget.value });
    this.search();
  }

  dialogClosing() {
    this.setState({ results: [], value: "" });
    this.props.closeCourseDialog();
  }
  render() {
    return (
      <Dialog open={this.props.open} onClose={this.dialogClosing}>
        <DialogTitle>Add Course</DialogTitle>
        <DialogContent className="course-dialog">
          <TextField
            helperText={<HelperText>Search</HelperText>} trailingIcon={<MaterialIcon role="button" icon="search" />}>
            <Input value={this.state.value} onChange={this.handleChange}></Input>
          </TextField>
          <List className="course-results-list" handleSelect={this.resultSelected}>
            {this.state.results.map(({ name, dept, course_num }, i) =>
              <ListItem key={i}>
                <ListItemText primaryText={`${dept}-${course_num}: ${name}`} />
              </ListItem>)}
          </List>
        </DialogContent>
        <DialogFooter>
          <DialogButton action="dismiss">Dismiss</DialogButton>
          <DialogButton action="accept" isDefault>
            Accept
          </DialogButton>
        </DialogFooter>
      </Dialog>
    );
  }
}

const mapStateToProps = (state) => {
  if (state.currScheduleName) {
    const schedule = state.schedules.get(state.currScheduleName);
    if (schedule) {
      return {
        currScheduleId: schedule.id
      }
    }
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    addCourse: (json) => dispatch(addCourse(json))
  }
};

export default connect(mapStateToProps, mapDispatchToProps)(AddCourseDialog);