import React from "react";
import ReactDOM from "react-dom";
import App from "./App";
import * as serviceWorker from "./serviceWorker";
import { Provider } from "react-redux";
import { createStore } from "redux";
import { reducer } from "./redux/reducers";

import "@material/react-drawer/dist/drawer.min.css";
import "@material/react-checkbox/dist/checkbox.min.css";
import "@material/react-list/dist/list.min.css";
import "@material/react-icon-button/dist/icon-button.min.css";
import "@material/react-material-icon/dist/material-icon.min.css";
import "@material/react-fab/dist/fab.min.css";
import "@material/react-dialog/dist/dialog.min.css";
import "@material/react-button/dist/button.min.css";
import "@material/react-select/dist/select.min.css";
import "@material/react-typography/dist/typography.min.css";
import "@material/react-text-field/dist/text-field.min.css";
import "@material/react-menu-surface/dist/menu-surface.min.css";

import "./App.scss";

const store = createStore(reducer);

ReactDOM.render(
  <Provider store={store}>
    <App />
  </Provider>,
  document.getElementById("root")
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
