import React from "react";
import List from "@material/react-list";

import CourseListItem from "./CourseListItem";
import { SchedulesState, Course, Section } from "../redux/types";
import { connect } from "react-redux";

interface CourseListProps {
  courses: { [key: string]: Course }
}
class CourseList extends React.Component<CourseListProps> {
  render() {
    let courseListItems: any[] = [];
    for (const key in this.props.courses) {
      const { dept, name, course_num, sections } = this.props.courses[key];
      const sectionProps = sections.map((section) => ({ section }));
      courseListItems.push(
        <CourseListItem
          key={`${dept}-${course_num}`}
          name={name}
          dept={dept}
          course_num={course_num}
          sections={sectionProps}
        />
      )
    }
    return (
      <List twoLine avatarList>
        {courseListItems}
      </List>
    );
  }
}

const mapStateToProps = (state: SchedulesState) => {
  let courses: { [key: string]: Course } = {};
  if (state.currScheduleName) {
    const schedule = state.schedules.get(state.currScheduleName);
    if (schedule) {
      courses = {...schedule.courses};
    }
  }
  return { courses };
}

export default connect(mapStateToProps)(CourseList);