import React, { ChangeEvent, FormEvent } from "react";
import Select from "@material/react-select";
import { selectSchedule } from "../redux/actions";
import { SchedulesState } from "../redux/types";
import { connect } from "react-redux";


interface ScheduleSelectProps {
  scheduleNames: string[];
  currScheduleName: string | undefined;
  selectSchedule: any;
}

class ScheduleSelect extends React.Component<ScheduleSelectProps> {
  constructor(props: ScheduleSelectProps) {
    super(props);
    this.handleSelectionChange = this.handleSelectionChange.bind(this);
  }

  componentDidMount() {
    if (!this.props.currScheduleName) {
      // HACK: Set choice to first schedule, probably bad UX, but can't really help it right now.
      if (this.props.scheduleNames.length > 0) {
        this.props.selectSchedule(this.props.scheduleNames[0]);
      }
    }
  }

  handleSelectionChange(evt: any) {
    if (evt.target) {
      this.props.selectSchedule(evt.target.value);
    } else {
      console.log("Event target is undefined?");
    }
  }

  render() {
    if (this.props.scheduleNames.length === 0) {
      return null;
    } else {
      return (
        <Select
          className="schedule-select"
          onChange={this.handleSelectionChange}
          options={this.props.scheduleNames}
        />
      );
    }
  }
}

const mapStateToProps = (state: SchedulesState) => ({
  currScheduleName: state.currScheduleName
});


const mapDispatchToProps = (dispatch: any) => {
  return {
    selectSchedule: (name: string) => dispatch(selectSchedule(name))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(ScheduleSelect);
