import React from "react";
import { Section, SchedulesState } from "../redux/types";
import MeetingBlock from "./MeetingBlock";
import { connect } from "react-redux";
import { Body1 } from "@material/react-typography";

interface WeekProps {
  sections: Array<Section>;
}

const stringifyTime = (minutesSinceMidnight: number): string => {
  let minutes = minutesSinceMidnight % 1;
  let hours = minutesSinceMidnight - minutes;
  minutes *= 60;
  minutes = Math.round(minutes);

  if (hours > 24) {
    var day = Math.floor(hours / 24);
    hours %= 24;
  }

  let date = new Date();
  date.setHours(hours);
  date.setMinutes(minutes);
  date.setSeconds(0);

  const time = date.toTimeString().split(" ")[0];
  const [hh, mm, ss] = time.split(":")

  return `${hh}:${mm}`;
};

const Week: React.SFC<WeekProps> = (props: WeekProps) => {
  const weekdays = [
    ["Monday", "M"],
    ["Tuesday", "T"],
    ["Wednesday", "W"],
    ["Thursday", "R"],
    ["Friday", "F"]
  ];
  const meetingsMap: { [key: string]: Array<React.ReactElement> } = {
    M: [],
    T: [],
    W: [],
    R: [],
    F: []
  };
  const END_TIME = 21; // 9pm
  const START_TIME = 7; // 7am
  const dayLength = END_TIME - START_TIME + 1;

  for (const section of props.sections) {
    for (const meeting of section.meetings) {
      if (meeting.start_time && meeting.end_time && meeting.meeting_days) {
        const start = (meeting.start_time - START_TIME * 60) / (dayLength * 60);
        const end = (meeting.end_time - START_TIME * 60) / (dayLength * 60);
        const duration = end - start;
        const displayText = `${section.dept}-${section.course_num}-${
          section.section_num
          }`;
        for (const day of meeting.meeting_days) {
          meetingsMap[day].push(
            <MeetingBlock
              key={Math.random() * Number.MAX_SAFE_INTEGER}
              start={start}
              duration={duration}
              displayText={displayText}
            />
          );
        }
      }
    }
  }

  const days = [];
  for (const [day, abbrev] of weekdays) {
    days.push(
      <div className="day" key={abbrev}>
        <strong className="week-heading">
          <Body1>{day}</Body1>
        </strong>
        {meetingsMap[abbrev]}
      </div>
    );
  }

  const rows = [];
  for (let i = 0; i <= dayLength; ++i) {
    rows.push(
      <tr className="week-row" key={i}>
        <td
          style={{
            width: "20px",
            height: i ? "auto" : "36px",
            color: "grey",
            textAlign: "left",
            padding: "3px 10px 0 0",
            paddingLeft: 10,
            borderBottom: "1px solid " + "grey",
            verticalAlign: "top"
          }}
        >
          <Body1>{i ? stringifyTime(i + 7 - 1) : ""}</Body1>
        </td>
        <td
          style={{
            borderBottom: "1px solid grey",
            boxSizing: "border-box"
          }}
        />
      </tr>
    );
  }
  return (
    <div>
      <div className="week-wrap">
        <div className="week-container">
          <table className="week-grid">
            <tbody>{rows}</tbody>
          </table>
          <div className="days">{days}</div>
        </div>
      </div>
    </div>
  );
};

const mapStateToProps = (state: SchedulesState) => {
  const sections: Array<Section> = [];
  if (state.currScheduleName) {
    const schedule = state.schedules.get(state.currScheduleName);
    if (schedule) {
      schedule.selectedCrns.forEach(crn => {
        for (const key in schedule.courses) {
          for (const section of schedule.courses[key].sections) {
            if (section.crn === crn) {
              sections.push(section);
            }
            if (sections.length === schedule.selectedCrns.size) {
              return { sections };
            }
          }
        }
      });
    }
  }
  return {
    sections
  };
};

export default connect(mapStateToProps)(Week);
